.debtorsList {
    margin: auto;
    width: 100%;
    padding: 0;
    text-align: center;
}

.item {
    width: 45%;
    margin: 5px 5px;
    display: inline-block;
}

@media (min-width: 600px) {
    .debtorsList {
        width: 100%;
        text-align: center;
    }

    .item {
        width: 32%;
    }
}

.debtorsList a {
    text-decoration: none;
    color: #120b31;
    font-weight: bold;
}

.debtorsList a:hover {
    color: #ae26c0;
}